'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import { toast } from 'sonner';
import { Plus } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { BahanBaku, Kategori, Satuan } from '@/types';

// Import Komponen Pecahan (Modular)
import InventoryFilters from '@/components/admin/bahan-baku/BahanBakuFilter';
import InventoryTable from '@/components/admin/bahan-baku/InventoryTable';
import InventoryFormDialog from '@/components/admin/bahan-baku/InventoryFormDialog';

export default function InventoryPage() {
    // --- 1. STATE MANAGEMENT ---
    
    // Data dari API
    const [bahans, setBahans] = useState<BahanBaku[]>([]);
    const [kategoris, setKategoris] = useState<Kategori[]>([]);
    const [satuans, setSatuans] = useState<Satuan[]>([]);
    const [loading, setLoading] = useState(true);

    // UI Control
    const [isDialogOpen, setIsDialogOpen] = useState(false);
    const [editData, setEditData] = useState<BahanBaku | null>(null);

    // Filter & Pagination
    const [searchQuery, setSearchQuery] = useState('');
    const [filterKategori, setFilterKategori] = useState<string>('all');
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage] = useState(10);

    // --- 2. DATA FETCHING ---

    const fetchAllData = async () => {
        setLoading(true);
        try {
            const [resBahan, resKat, resSat] = await Promise.all([
                axios.get('/api/bahanBakus'),
                axios.get('/api/kategoris'),
                axios.get('/api/satuans')
            ]);
            setBahans(resBahan.data);
            setKategoris(resKat.data);
            setSatuans(resSat.data);
        } catch (error) {
            console.error(error);
            toast.error("Gagal memuat data inventaris.");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => { fetchAllData(); }, []);

    // --- 3. LOGIC (Filter & Pagination) ---

    const filteredBahans = bahans.filter((item) => {
        const matchName = item.nama_bahan_baku.toLowerCase().includes(searchQuery.toLowerCase());
        const matchCategory = filterKategori === 'all' || item.kategori_id.toString() === filterKategori;
        return matchName && matchCategory;
    });

    // Reset halaman ke 1 saat filter berubah
    useEffect(() => { setCurrentPage(1); }, [searchQuery, filterKategori]);

    // Slice data untuk pagination
    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentItems = filteredBahans.slice(indexOfFirstItem, indexOfLastItem);

    // --- 4. HANDLERS (Aksi User) ---

    const handleCreate = () => {
        setEditData(null);
        setIsDialogOpen(true);
    };

    const handleEdit = (item: BahanBaku) => {
        setEditData(item);
        setIsDialogOpen(true);
    };

    const handleDelete = async (id: number) => {
        if(!confirm("Hapus bahan baku ini? Data yang dihapus tidak bisa dikembalikan.")) return;
        try {
            await axios.delete(`/api/bahanBakus/${id}`);
            toast.success("Bahan baku berhasil dihapus");
            fetchAllData(); // Refresh data
        } catch (error) {
            toast.error("Gagal menghapus bahan baku (Mungkin sedang digunakan dalam resep/transaksi).");
        }
    };

    const handleSubmit = async (formData: any, isEdit: boolean) => {
        try {
            if (isEdit && editData) {
                // --- UPDATE MODE ---
                
                // 1. Update Data Teks (PUT JSON)
                await axios.put(`/api/bahanBakus/${editData.id}`, formData);
                
                // 2. Update Foto (POST FormData) - Jika ada file baru
                if (formData.foto) {
                    const imgData = new FormData();
                    imgData.append('foto', formData.foto);
                    // Biarkan axios handle boundary multipart secara otomatis
                    await axios.post(`/api/bahanBakus/${editData.id}/foto`, imgData);
                }
                
                toast.success("Bahan baku berhasil diperbarui!");

            } else {
                // --- CREATE MODE ---
                
                // Kirim semua sebagai FormData karena ada file upload
                const data = new FormData();
                
                // Loop object formData dan masukkan ke FormData object
                Object.keys(formData).forEach(key => {
                    if (formData[key] !== null && formData[key] !== undefined) {
                        data.append(key, formData[key]);
                    }
                });

                await axios.post('/api/bahanBakus', data);
                toast.success("Bahan baku berhasil ditambahkan!");
            }

            setIsDialogOpen(false);
            fetchAllData(); // Refresh tabel
            
        } catch (error: any) {
            console.error(error);
            // Tampilkan pesan error yang spesifik dari backend jika ada
            const message = error.response?.data?.message || "Gagal menyimpan data.";
            toast.error(message);
        }
    };

    // --- 5. RENDER UI ---

    return (
        <div className="space-y-6">
            {/* Header Section */}
            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Stok Bahan Baku</h1>
                    <p className="text-gray-500 text-sm">Kelola inventaris dapur dan bar.</p>
                </div>
                <Button className="bg-[#D93F21] hover:bg-[#b9351b]" onClick={handleCreate}>
                    <Plus className="w-4 h-4 mr-2" /> Tambah Bahan
                </Button>
            </div>

            {/* Komponen Filter */}
            <InventoryFilters 
                searchQuery={searchQuery}
                setSearchQuery={setSearchQuery}
                filterKategori={filterKategori}
                setFilterKategori={setFilterKategori}
                kategoris={kategoris}
            />

            {/* Komponen Tabel */}
            <InventoryTable 
                loading={loading}
                bahans={currentItems}
                totalData={filteredBahans.length}
                currentPage={currentPage}
                itemsPerPage={itemsPerPage}
                onPageChange={setCurrentPage}
                onEdit={handleEdit}
                onDelete={handleDelete}
            />

            {/* Komponen Modal Form */}
            <InventoryFormDialog 
                isOpen={isDialogOpen}
                onOpenChange={setIsDialogOpen}
                onSubmit={handleSubmit}
                initialData={editData}
                kategoris={kategoris}
                satuans={satuans}
            />
        </div>
    );
}