'use client';

import { Search, X } from 'lucide-react';
import { Input } from '@/components/ui/input';
import { Button } from '@/components/ui/button';
import {
    Select,
    SelectContent,
    SelectItem,
    SelectTrigger,
    SelectValue,
} from "@/components/ui/select";
import { Kategori } from '@/types';

interface BahanBakuFilterProps {
    searchQuery: string;
    setSearchQuery: (value: string) => void;
    filterKategori: string;
    setFilterKategori: (value: string) => void;
    kategoris: Kategori[];
}

export default function BahanBakuFilter({
    searchQuery,
    setSearchQuery,
    filterKategori,
    setFilterKategori,
    kategoris
}: BahanBakuFilterProps) {
    return (
        <div className="flex flex-col md:flex-row gap-4 p-4 border border-gray-200 rounded-lg shadow-sm bg-white">
            <div className="flex-1 relative">
                <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-gray-500" />
                <Input
                    placeholder="Cari bahan baku..."
                    className="pl-9"
                    value={searchQuery}
                    onChange={(e) => setSearchQuery(e.target.value)}
                />
            </div>
            <div className="w-full md:w-[250px]">
                <Select value={filterKategori} onValueChange={setFilterKategori}>
                    <SelectTrigger><SelectValue placeholder="Filter Kategori" /></SelectTrigger>
                    <SelectContent>
                        <SelectItem value="all">Semua Kategori</SelectItem>
                        {kategoris.map((k) => (
                            <SelectItem key={k.id} value={k.id.toString()}>{k.nama_kategori}</SelectItem>
                        ))}
                    </SelectContent>
                </Select>
            </div>
            {(searchQuery || filterKategori !== 'all') && (
                <Button variant="ghost" onClick={() => { setSearchQuery(''); setFilterKategori('all'); }} className="text-gray-500">
                    <X className="h-4 w-4 mr-2" /> Reset
                </Button>
            )}
        </div>
    );
}